import * as THREE from 'three'

class Environment 
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.debug = this.experience.debug

        // if(this.debug.active)
        // {
        //     this.envFolder = this.debug.gui.addFolder('Environment')
        // }

        this.setEnvironment()
        // this.setEnvironmentMap()
    }

    setEnvironment()
    {
        this.directional = new THREE.DirectionalLight(0x000055 , 3.8)
        this.directional.castShadow = true
        this.directional.shadow.camera.far = 15
        this.directional.shadow.mapSize.set(1024,1024)
        this.directional.shadow.normalBias = 0.05
        this.directional.position.set(0, -7.5,1.5)

        this.scene.add(this.directional)
    }

    setEnvironmentMap()
    {
        this.environmentMap = {}
        this.environmentMap.intensity = 1.4
        this.environmentMap.texture = this.resources.items.environmentMapTexture
        this.environmentMap.texture.encoding = THREE.sRGBEncoding

        this.scene.environment = this.environmentMap.texture

        this.environmentMap.updateMaterials = () =>
        {
            this.scene.traverse((child) =>
            {
                if(child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial)
                {
                    child.material.envMap = this.environmentMap.texture
                    child.material.envMapIntensity = this.environmentMap.intensity
                    child.material.needsUpdate = true
                }
            })
        }
        this.environmentMap.updateMaterials()

        // Debug
        this.envFolder
            .add(this.environmentMap , 'intensity')
            .min(0)
            .max(5)
            .step(0.01)
            .name('Environment intensity')
            .onChange(()=>{this.environmentMap.updateMaterials()})

    }
}

export default Environment