import gsap from 'gsap'

// Progress bar
const progressLinks = document.querySelectorAll('.progress-div')
const progressText = document.querySelector('.progress-text')

export function styleProgressLinks (linkId , textContent){
    for(const link of progressLinks){
        progressText.textContent = textContent
        progressText.style.color = 'white'
        if(link.id == linkId) {
            link.style.background = 'white'
            link.style.height = '3px'
        }
        else {
            link.style.background = 'grey'
            link.style.height = '1px'
        }
    }
}

// Clear loading plane
export function clearLoadingPlane(){
    gsap.to('.loading-plane' , {duration:0.5, x:window.innerWidth})
}