import './style/style.css'
import './style/sectionsStyle.css'
import './style/pages.css'

import * as THREE from 'three'
import gsap from 'gsap'

// const fileContent = require("php!login.php")
// console.log(fileContent)

import vertex from './shaders/vertex.glsl'
import fragment from './shaders/fragment.glsl'
import backPointsVertex from './shaders/backPointsVertex.glsl'
import backPointsFragment from './shaders/backPointsFragment.glsl'
import textureVertex from './shaders/textureVertex.glsl'
import textureFragment from './shaders/textureFragment.glsl'
import logosVertex from './shaders/logosVertex.glsl'
import logosFragment from './shaders/logosFragment.glsl'
import mouseVertex from './shaders/mouseVertex.glsl'
import headFragment from './shaders/headFragment.glsl'
import projectsVertex from './shaders/projectsVertex.glsl'
import projectsFragment from './shaders/projectsFragment.glsl'
import headVertex from './shaders/headVertex.glsl'


import Experience from './Experience/Experience.js'

    //Create experience
    const experience = new Experience(
        document.querySelector('canvas.webgl'),
        vertex, 
        fragment, 
        textureVertex,
        textureFragment,
        backPointsVertex,
        backPointsFragment,
        logosVertex,
        logosFragment,
        mouseVertex,
        headFragment,
        projectsVertex,
        projectsFragment,
        headVertex,
    )

    // Projects section mouseover effects
    const projects = document.querySelectorAll('.projects')
    const projectsDesc = document.querySelectorAll('.projects-text')
    for(const p of projects)
    {
        p.addEventListener('click', ()=>{
            gsap.to(experience.world.projectMesh.material.uniforms.uAmplifier,{duration:0.5, value:3})
            if(p.id == 'project-1')
            {
                showProjectsText(0)
                experience.world.environment.directional.color = new THREE.Color(0x0055ff)
                experience.world.mouseMesh.scale.set(5,5,5)
                experience.world.mouseMesh.material.color = new THREE.Color(0x1111ff)
                experience.world.projectMesh.material.uniforms.uTexture.value =  experience.resources.items.project1Texture
            }
            else if(p.id == 'project-2')
            {
                showProjectsText(1)
                experience.world.raycasterPlane.material.color = new THREE.Color(0xeb0fbb)
                experience.world.environment.directional.color = new THREE.Color(0xeb0fbb)
                experience.world.mouseMesh.scale.set(5,5,5)
                experience.world.mouseMesh.material.color = new THREE.Color(0x110011)
                experience.world.projectMesh.material.uniforms.uTexture.value =  experience.resources.items.project2Texture
            }
            else if(p.id == 'project-3')
            {
                showProjectsText(2)
                experience.world.raycasterPlane.material.color = new THREE.Color(0xcf752a)
                experience.world.environment.directional.color = new THREE.Color(0xcf752a)
                experience.world.mouseMesh.scale.set(5,5,5)
                experience.world.mouseMesh.material.color = new THREE.Color(0xff1100)
                experience.world.projectMesh.material.uniforms.uTexture.value =  experience.resources.items.project3Texture
            }
            else if(p.id == 'project-4')
            {
                showProjectsText(3)
                experience.world.raycasterPlane.material.color = new THREE.Color(0x8c6e85)
                experience.world.environment.directional.color = new THREE.Color(0x8c6e85)
                experience.world.mouseMesh.scale.set(5,5,5)
                experience.world.mouseMesh.material.color = new THREE.Color(0x111111)
                experience.world.projectMesh.material.uniforms.uTexture.value =  experience.resources.items.project4Texture
            }
            else if(p.id == 'project-5')
            {
                showProjectsText(4)
                experience.world.raycasterPlane.material.color = new THREE.Color(0x19485d)
                experience.world.environment.directional.color = new THREE.Color(0x19485d)
                experience.world.mouseMesh.scale.set(5,5,5)
                experience.world.mouseMesh.material.color = new THREE.Color(0x1122cc)
                experience.world.projectMesh.material.uniforms.uTexture.value =  experience.resources.items.project5Texture
            }
    })

    // p.addEventListener('mouseout', ()=>{
    //     experience.world.environment.directional.color = new THREE.Color(0x000055)
    //     gsap.to(experience.world.projectMesh.material.uniforms.uAmplifier,{duration:0.5, value:1})
    //     experience.world.mainMesh.material.uniforms.uColor1.value = new THREE.Color(0x66ff47)
    //         experience.world.mouseMesh.scale.set(1,1,1)
    //         experience.world.mouseMesh.material.color = new THREE.Color(0xffffff)
    // })
}

    // Displaying the corresponding text for each project hovered
    const showProjectsText=(index)=>
    {
        for(let i=0;i<projectsDesc.length;i++)
        {
            if(i==index)
            {
                projectsDesc[i].style.display = 'flex'
            }
            else
            {
                projectsDesc[i].style.display = 'none'
            }
        }
    }




