import * as THREE from 'three'
import Experience from './Experience.js'
import {EffectComposer} from 'three/examples/jsm/postprocessing/EffectComposer.js'
import {RenderPass} from 'three/examples/jsm/postprocessing/RenderPass.js'
import {RGBShiftShader} from 'three/examples/jsm/shaders/RGBShiftShader'
import {ShaderPass} from 'three/examples/jsm/postprocessing/ShaderPass.js'
import {UnrealBloomPass} from 'three/examples/jsm/postprocessing/UnrealBloomPass.js'

export default class Renderer
{
    constructor()
    {
        this.experience = new Experience()
        this.canvas = this.experience.canvas
        this.sizes = this.experience.sizes
        this.scene = this.experience.scene
        this.camera = this.experience.camera
                
        this.setInstance()
        this.createComposer()
    }

    setInstance()
    {
        this.instance = new THREE.WebGLRenderer({
            canvas: this.canvas,
            antialias: true
        })
        this.instance.physicallyCorrectLights = true
        this.instance.outputEncoding = THREE.sRGBEncoding
        this.instance.toneMapping = THREE.CineonToneMapping
        this.instance.toneMappingExposure = 1.75
        this.instance.shadowMap.enabled = true
        this.instance.shadowMap.type = THREE.PCFSoftShadowMap
        this.instance.setClearColor(0x04007a)
        this.instance.setSize(this.sizes.width, this.sizes.height)
        this.instance.setPixelRatio(Math.min(this.sizes.pixelRatio, 2))
    }

    createComposer()
    {
        this.composer = new EffectComposer(this.instance)
        this.composer.setPixelRatio(Math.min(window.devicePixelRatio , 2))
        this.composer.setSize(this.sizes.width , this.sizes.height)

        this.renderPass = new RenderPass(this.scene, this.camera.instance)
        this.composer.addPass(this.renderPass)

        this.rgbShiftPass = new ShaderPass(RGBShiftShader)
        this.composer.addPass(this.rgbShiftPass)
        this.rgbShiftPass.enabled = false

        this.unrealBloom = new UnrealBloomPass()
        this.unrealBloom.strength = 1.4
        this.unrealBloom.threshold = 0.01
        this.unrealBloom.radius = 0.01
        this.composer.addPass(this.unrealBloom)
        // this.unrealBloom.enabled = false
    }

    resize()
    {
        //renderer
        this.instance.setSize(this.sizes.width, this.sizes.height)
        this.instance.setPixelRatio(Math.min(this.sizes.pixelRatio, 2))
        //composer
        this.composer.setSize(this.sizes.width, this.sizes.height)
        this.composer.setPixelRatio(Math.min(this.sizes.pixelRatio, 2))
    }

    update()
    {
        // this.instance.render(this.scene, this.camera.instance)
        this.composer.render()
    }
}