import gsap from 'gsap'

class HoveredElements
{
    constructor(experience)
    {
        this.experience = experience
        this.hoveredElements = document.querySelectorAll('.hovered-elements')

        this.createInstance()
    }

    createInstance()
    {
        for(const e of this.hoveredElements)
        {
            e.addEventListener('mouseover',()=>
            {
                gsap.to(this.experience.world.mouseMesh.scale,{duration:0.2,x:3})
                gsap.to(this.experience.world.mouseMesh.scale,{duration:0.2,y:3})
            })
            e.addEventListener('mouseout',()=>
            {
                gsap.to(this.experience.world.mouseMesh.scale,{duration:0.2,x:1})
                gsap.to(this.experience.world.mouseMesh.scale,{duration:0.2,y:1})
            })
        }
    }
}

export default HoveredElements

