import * as THREE from 'three'

import Sizes from './Utils/Sizes.js'
import Time from './Utils/Time.js'
import Camera from './Camera.js'
import Renderer from './Renderer.js'
import World from './World/World.js'
import Resources from './Utils/Resources.js'
import Mouse from './Utils/Mouse.js'
import Buttons from './Utils/Buttons.js'
import HoveredElements from './Utils/HoveredElements.js'
import ModalButtons from './Utils/ModalButtons.js'
import MenuLinks from './Utils/MenuLinks.js'
import SkillsButtons from './Utils/SkillsButtons.js'


import sources from './sources.js'

let instance = null

export default class Experience
{
    constructor(
        _canvas, vertex, fragment,textureVertex,
        textureFragment,backPointsVertex,backPointsFragment,
        logosVertex,logosFragment,mouseVertex,
        headFragment,projectsVertex,projectsFragment,
        headVertex)
    {
        // Singleton
        if(instance)
        {
            return instance
        }
        instance = this
        
        // Global access & shaders
        window.experience = this
        this.vertex = vertex
        this.fragment = fragment
        this.textureVertex = textureVertex
        this.textureFragment = textureFragment
        this.backPointsV = backPointsVertex
        this.backPointsF = backPointsFragment
        this.logosVertex = logosVertex
        this.logosFragment = logosFragment
        this.mouseVertex = mouseVertex
        this.headFragment = headFragment
        this.projectsVertex = projectsVertex
        this.projectsFragment = projectsFragment
        this.headVertex = headVertex


        // Options
        this.canvas = _canvas

        // Setup
        // this.debug = new Debug()
        this.sizes = new Sizes()
        this.time = new Time()
        this.scene = new THREE.Scene()
        this.resources = new Resources(this , sources)
        this.camera = new Camera()
        this.renderer = new Renderer()
        this.world = new World(this)
        this.buttons = new Buttons(this)
        this.hoveredElements = new HoveredElements(this)
        this.modalButtons = new ModalButtons()
        this.menuLinks = new MenuLinks(this)
        this.skillsButtons = new SkillsButtons(this)


        // Resize event
        this.sizes.on('resize', () =>
        {
            this.resize()
        })

        // Time tick event
        this.time.on('tick', () =>
        {
            this.update()
        })

    }

    resize()
    {
        this.camera.resize()
        this.renderer.resize()
    }

    update()
    {
        this.world.update()
        this.renderer.update()
    }

    destroy()
    {
        this.sizes.off('resize')
        this.time.off('tick')

        //Traverse scene
        this.scene.traverse((child)=>
        {
            if(child instanceof THREE.Mesh){
                child.geometry.dispose()

                for(const key in child.material)
                {
                    const value = child.material[key]

                    if(value && typeof value.dispose === 'function')
                    {
                        value.dispose()
                    }
                }
            }
        })

        this.camera.controls.dispose()
        this.renderer.instance.dispose()
        if(this.debug.active)
        {
            this.debug.gui.destroy()
        }

    }
}