import gsap from 'gsap'

class MenuLinks
{
    constructor(experience)
    {
        this.experience = experience
        this.links = document.querySelectorAll('.menu-links')

        this.createLinksClick()
    }

    createLinksClick()
    {
        for(const link of this.links)
        {
            link.addEventListener('click',(e)=>
            {
                e.preventDefault()
                if(link.id === 'home-link')
                {
                    this.closeMenuLinks()
                }
                else if(link.id == "contact-link")
                {
                    this.closeMenuLinks()
                    window.scrollTo({
                        top:this.experience.sizes.height * 5,
                        left:0,
                        behavior:'smooth'
                    })
                }
                else
                {
                    this.closeMenuLinks()
                    window.scrollTo({
                        top:this.experience.sizes.height,
                        left:0,
                        behavior:'smooth'
                    })
                }
            })
        }
    }

    closeMenuLinks()
    {
        document.body.style.overflowY = 'auto'
        gsap.to('.work-btn',{duration:0.5,opacity:1})
        gsap.to('.nav-contact',{duration:0.5,opacity:1})
        gsap.to('.socials-links',{duration:0.5,opacity:1})
        gsap.to('.section1',{duration:0.5,opacity:1})
        gsap.to('.progress',{duration:0.5,opacity:1})
        gsap.to(this.experience.camera.instance.rotation,{duration:0.5,y:Math.PI*2})
        gsap.to(this.experience.world.backPoints.position,{duration:0.5,z:0})
        gsap.to(this.experience.world.backPointsOne.position,{duration:0.5,z:-5})
        gsap.to('.menu',{duration:0.5 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 100%,0% 100%)'})
        this.experience.buttons.sidebarState = false
    }
}

export default MenuLinks