import gsap from 'gsap'

class Buttons 
{
    constructor(experience)
    {
        this.experience = experience
        
        // Properties
        this.openMenuBtn = document.querySelector('.sidebar')
        this.menu = document.querySelector('.menu')
        this.workBtn = document.querySelector('.work-btn')
        this.contactBtn = document.querySelector('.nav-contact')
        this.sidebarState = false

        // Call methods
        this.createMenuButtonListener()
        this.createWorkButtonListener()
        this.createContactButtonClick()
    }

    // Create event for menu button click
    createMenuButtonListener()
    {
        this.openMenuBtn.addEventListener('click',()=>
        {
            if(this.sidebarState == false)
            {
                document.body.style.backgroundColor = 'rgba(0,0,0,0.6)'
                document.body.style.overflowY = 'hidden'
                gsap.to('.work-btn',{duration:0.5,opacity:0})
                gsap.to('.nav-contact',{duration:0.5,opacity:0})
                gsap.to('.socials-links',{duration:0.5,opacity:0})
                gsap.to('.section1',{duration:0.5,opacity:0})
                gsap.to('.progress',{duration:0.5,opacity:0})
                gsap.to(this.experience.camera.instance.rotation,{duration:0.5,y:Math.PI})
                gsap.to(this.experience.world.backPoints.position,{duration:0.5,z:1})
                gsap.to(this.experience.world.backPointsOne.position,{duration:0.5,z:5})
                gsap.to('.menu',{duration:0.5 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 0%,0% 0%)'})
                this.sidebarState = true
            }
            else
            {
                document.body.style.overflowY = 'auto'
                gsap.to('.work-btn',{duration:0.5,opacity:1})
                gsap.to('.nav-contact',{duration:0.5,opacity:1})
                gsap.to('.socials-links',{duration:0.5,opacity:1})
                gsap.to('.section1',{duration:0.5,opacity:1})
                gsap.to('.progress',{duration:0.5,opacity:1})
                gsap.to(this.experience.camera.instance.rotation,{duration:0.5,y:Math.PI*2})
                gsap.to(this.experience.world.backPoints.position,{duration:0.5,z:0})
                gsap.to(this.experience.world.backPointsOne.position,{duration:0.5,z:-5})
                gsap.to('.menu',{duration:0.5 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 100%,0% 100%)'})
                this.sidebarState = false
            }
        })
    }

    // Create event for 'work' button click
    createWorkButtonListener()
    {
        this.workBtn.addEventListener('click',()=>{
            window.scrollTo({
                top:this.experience.sizes.height * 3,
                left:0,
                behavior:'smooth'
            })
        })
    }

    // Create event for 'contact' button click
    createContactButtonClick()
    {
        this.contactBtn.addEventListener('click',()=>{
            window.scrollTo({
                top:this.experience.sizes.height * 4,
                left:0,
                behavior:'smooth'
            })
        })
    }
}

export default Buttons
