class ModalButtons
{
    constructor()
    {
        // DOM elements
        this.modalButtons = document.querySelectorAll('.section2-info')
        this.modal = document.querySelector('.section2-info-modal')
        this.closeModalButton = document.querySelector('.close-section2-modal-btn')

        // Data for modals
        this.modalData = 
        [
            {
                title:'Education',
                info:`I graduaded from the Technological Educational Institute of Crete, with a BSc in IT Enginnering. I had the oppurtunity to work with different programming languages, such as C++ , Java & more as well as gain an all round knowledge about IT.
                Later on i decided to focus mainly on web development. My goal lately has been to master 3D Designing and create immersive experiences.`
            },
            {
                title:'Future',
                info:`As my web development journey is currently shorter than a year, i intent to get my hands on as many technologies as possible and create the best web expieriences a client can have. Make sure to stick with me.`
            }
        ]

        // Initiating methods
        this.createModalButtonClick()
        this.createModalCloseClick()
    }

    // Click event for opening modals
    createModalButtonClick()
    {
        for(const btn of this.modalButtons)
        {
            btn.addEventListener('click',()=>
            {
                this.modal.style.transform = 'scale(1) translate(-50%,-50%)'
                if(btn.id == 'education')
                {
                    this.modal.children[0].textContent = this.modalData[0].title
                    this.modal.children[1].textContent = this.modalData[0].info
                }
                else
                {
                    this.modal.children[0].textContent = this.modalData[1].title
                    this.modal.children[1].textContent = this.modalData[1].info
                }
            })
        }
    }

    // Click event for closing modals
    createModalCloseClick()
    {
        this.closeModalButton.addEventListener('click',(e)=>
        {
            this.modal.style.transform = 'scale(0)'
        })
    }

}

export default ModalButtons