import * as THREE from 'three'
import gsap from "gsap"
import {styleProgressLinks} from './Functions.js'

class Mouse 
{
    constructor(experience)
    {
        this.experience = experience
        this.sizes = this.experience.sizes
        this.camera = this.experience.camera.instance

        //Mouse coordinates & raycaster
        this.raycaster = new THREE.Raycaster()
        this.mouse = new THREE.Vector2()
        this.eMouse = new THREE.Vector2(0,0)
        this.elasticMouse = new THREE.Vector2(0,0)
        this.elasticMouseVelocity = new THREE.Vector2(0,0)
        this.tempPosition = new THREE.Vector2(0,0)


        if(this.experience.world.mainMesh)
        {
        }
        this.mouseMove()
        this.mouseScroll()
    }

    // Event listener for mouse move
    mouseMove()
    {
        window.addEventListener('mousemove' , (e)=>
        {
            const cursor = e.clientX/this.experience.sizes.width

            //Update uniforms on mousemove
            // this.experience.world.mainMesh.material.uniforms.uAmplifier.value = cursor

            //Update mouse coordinates
            this.mouse.x = (e.clientX / this.experience.sizes.width) * 2 - 1
            this.mouse.y = - (e.clientY / this.experience.sizes.height) * 2 + 1

             //Update uniforms on mousemove
            this.experience.world.backPointsOne.position.x = - this.mouse.x / 8
            this.experience.world.backPoints.position.y = - this.mouse.y / 10

            this.eMouse.x = e.clientX
            this.eMouse.y = e.clientY

            // Move model on mousemove
            this.experience.world.headMesh.rotation.z = this.mouse.x /2

            // Raycaster
            this.raycaster.setFromCamera(this.mouse , this.camera)

            // Set raycaster for background plane
            const intersects = this.raycaster.intersectObject(this.experience.world.raycasterPlane)
            if(intersects.length)
            {
                    let point = intersects[0].point
                    this.experience.world.mouseMesh.position.copy(point)
                    this.eMouse.x = point.x
                    this.eMouse.y = point.y
            }

            //Set raycaster for projects images
            const instersectsProjects = this.raycaster.intersectObject(this.experience.world.projectMesh)
            if(instersectsProjects.length)
            {
                gsap.to(this.experience.world.projectMesh.scale, {duration:0.5 , x:1.3})
                gsap.to(this.experience.world.projectMesh.scale, {duration:0.5 , y:1.3})
            }
            else
            {
                gsap.to(this.experience.world.projectMesh.scale, {duration:0.5 , x:1})
                gsap.to(this.experience.world.projectMesh.scale, {duration:0.5 , y:1})
            }

            //Set raycaster for skills mesh
            const intersectsSkills = this.raycaster.intersectObject(this.experience.world.skillsMesh)
            if(intersectsSkills.length)
            {
                gsap.to(this.experience.world.skillsMesh.rotation, {duration:0.5, x:0})
                // gsap.to(this.experience.world.skillsMesh.scale, {duration:0.5, x:1.5})
                // gsap.to(this.experience.world.skillsMesh.scale, {duration:0.5, y:1.5})
                // gsap.to(this.experience.world.skillsMesh.scale, {duration:0.5, z:1.5})
            }
            else {
                gsap.to(this.experience.world.skillsMesh.rotation, {duration:0.5, x:0.2})
                gsap.to(this.experience.world.skillsMesh.scale, {duration:0.5, x:1})
                gsap.to(this.experience.world.skillsMesh.scale, {duration:0.5, y:1})
                gsap.to(this.experience.world.skillsMesh.scale, {duration:0.5, z:1})
            }

            //Set raycaster for projects slider
            const intersectsProjectSlider = this.raycaster.intersectObject(this.experience.world.projectUniversalShape)
            if(intersectsProjectSlider.length)
            {
                gsap.to(this.experience.world.projectUniversalShape.scale,{duration:0.5,y:1.5})
                gsap.to(this.experience.world.projectUniversalShape.scale,{duration:0.5,x:1.5})
                gsap.to(this.experience.world.projectUniversalShape.material.uniforms.uNegator,{duration:0.5,value:0})
            }
            else
            {
                gsap.to(this.experience.world.projectUniversalShape.scale,{duration:0.5,y:1})
                gsap.to(this.experience.world.projectUniversalShape.scale,{duration:0.5,x:1})
                gsap.to(this.experience.world.projectUniversalShape.material.uniforms.uNegator,{duration:0.5,value:1})
            }

            // Set raycaster for mail model
            // const intersectsMailModel = this.raycaster.intersectObject(this.experience.world.model.mailModel)
            // if(intersectsMailModel.length)
            // {
            //     gsap.to(this.experience.world.model.mailModel.scale,{duration:0.5,y:0.6})
            //     gsap.to(this.experience.world.model.mailModel.scale,{duration:0.5,x:0.6})
            // }
            // else
            // {
            //     gsap.to(this.experience.world.model.mailModel.scale,{duration:0.5,y:0.4})
            //     gsap.to(this.experience.world.model.mailModel.scale,{duration:0.5,x:0.4})
            // }
        })
    }

    // Event listener for mouse scrolling
    mouseScroll()
    {
        let scroll = 0
        window.addEventListener('scroll' , ()=>{
            scroll = window.scrollY / this.sizes.height

            this.camera.position.y = -scroll * 2

            //Starting scroll events
            if(scroll>=0 && scroll<0.8)
            {
                styleProgressLinks('progress-0' , 'Home')
                gsap.to('.section1-scroll',{duration:0.5 , opacity:0})
                this.experience.world.backPoints.visible = true
                this.experience.world.backPointsOne.visible = true
            }

            // About section scrolling events
            if(scroll>0.8 && scroll<1.8)
            {
                styleProgressLinks('progress-1' , 'About')
                gsap.to('.about-content',{duration:0.5 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 0%,0% 0%)', y:0})
                gsap.to('#education' , {duration:1 , opacity:0.7})
                gsap.to('#future' , {duration:1 , opacity:0.7})
                gsap.to(this.experience.world.headMesh.position,{duration:1 , z:0, ease: "back.out(1.7)"})
                this.experience.world.mainMesh.visible = false
                gsap.to(this.experience.world.mainMesh.material.uniforms.uAmplifier,{duration:0.5 , value: 0.3})
            }
            else 
            {
                this.experience.world.mainMesh.visible = true
                gsap.to(this.experience.world.headMesh.position,{duration:0.5 , z:4})
                gsap.to('#education' , {duration:1 , opacity:0})
                gsap.to('#future' , {duration:1 , opacity:0})
                gsap.to(this.experience.world.mainMesh.material.uniforms.uAmplifier,{duration:0.5 , value: 0.5}) 
            }

            // Skills section scrolling events
            if(scroll>1.8 & scroll<2.8)
            {
                gsap.to(this.experience.world.skillsMesh.position, {duration:1 , z:0 ,ease: "back.out(1.7)"})
                gsap.to('.section3-content',{duration:0.5 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 0%,0% 0%)', y:0})
                styleProgressLinks('progress-2' , 'Skills')
                gsap.to('#approach' , {duration:1 , opacity:0.7})
                gsap.to('#skills' , {duration:1 , opacity:0.7})
                gsap.to(this.experience.camera.instance.position , {duration:0.5 , z:1})
            }
            else
            {
                gsap.to(this.experience.world.skillsMesh.position, {duration:1 , z:6})
                gsap.to(this.experience.world.mainMesh.material.uniforms.uBrightness, {duration:0.5 , value:0.1})
                gsap.to(this.experience.camera.instance.position , {duration:0.5 , z:2})
                gsap.to('#approach' , {duration:1 , opacity:0})
                gsap.to('#skills' , {duration:1 , opacity:0})
            }

            // Projects section scrolling events
            if(scroll > 2.8 & scroll<3.8)
            {
                gsap.to('.project-titles',{duration:0.5 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 0%,0% 0%)', y:0})
                styleProgressLinks('progress-3' , 'Work')
                gsap.to(this.experience.world.mainMesh.material.uniforms.uExpander, {duration:0.5 , value:1})
                gsap.to(this.experience.world.mainMesh.position, {duration:0.5, y:-3.5})
                gsap.to(this.experience.world.mainMesh.position, {duration:0.5, x:-3})
                gsap.to(this.experience.renderer.unrealBloom, {duration:0.5,enabled:false})
            }
            else
            {
                this.experience.world.environment.directional.color = new THREE.Color(0x000055)
                this.experience.world.raycasterPlane.material.color = new THREE.Color(0x04002a)
                gsap.to(this.experience.world.mainMesh.material.uniforms.uExpander, {duration:0.5 , value:1})
                gsap.to(this.experience.world.mainMesh.position, {duration:0.5, y:-1})
                gsap.to(this.experience.world.mainMesh.position, {duration:0.5, x:1.5})
                gsap.to(this.experience.renderer.unrealBloom, {duration:0.5,enabled:true})
            }

            // Contact section scrolling events
            if(scroll>3.8)
            {
                gsap.to(this.experience.renderer.unrealBloom, {duration:0.5,enabled:false})
                styleProgressLinks('progress-4' , 'Contact')
                gsap.to(this.experience.renderer.unrealBloom, {duration:0.5,strength:0.5})
                gsap.to(this.experience.world.backPoints.position , {duration:0.5 , y:-5})
                gsap.to(this.experience.world.backPointsOne.position , {duration:0.5 , y:-5})
            }
            else
            {
                gsap.to(this.experience.world.backPoints.position , {duration:0.5 , y:0})
                gsap.to(this.experience.world.backPointsOne.position , {duration:0.5 , y:0})
            }
        })
    }

    update()
    {
        // this.tempPosition.copy(this.eMouse).sub(this.elasticMouse).myltiplyScalar(0.15)
    }
}

export default Mouse