import gsap from 'gsap'

class SkillsButtons
{
    constructor(experience)
    {
        this.experience = experience

        // DOM elements
        this.skillsDetails = document.querySelector('.section3-skills-details')
        this.closeSkillsButton = document.querySelector('.close-skills-btn')
        this.skillsNextButton = document.querySelector('.next-skill-btn')
        this.section3Buttons = document.querySelectorAll('.section3-buttons')

        // counter
        this.skillsCounter = 0
        this.approachCounter = 0

        // Data for 'skills' details
        this.skillsData = 
        [
            {
                title:'CSS, HTML & Javascript',
                content:'to create the basic setup & functionality of the page along with the stylying.'
            },
            {
                title:'three.js, webGL & GLSL',
                content:'for 3D functionality and visuals of the application.'
            },
            {
                title:'Cannon.js',
                content:'for adapting real world physics in my projects and determine the behaviour of objects.'
            },
            {
                title:'React.js & Firebase',
                content:'for creating component based application and managing the client-side databases'
            },
            {
                title:'Webpack & npm',
                content:'for the development environment & bundling my applications'
            },
            {
                title:'Blender',
                content:'for creating 3D models which i can later manipulate and integrate in my projects'
            },
            {
                title:'GSAP',
                content:'for creating basic smooth animations'
            }
        ]

        this.approachData = 
        [
            {
                title:'Approach - 01',
                content:`I use webpack to bundle my Javascript applications.`
            },
            {
                title:'Approach - 02',
                content:`I seperate everything into classes and modules to make my code editable and comprehensive.`
            },
            {
                title:'Approach - 03',
                content:`On the creative side, i try to come up with a picture in my head of the main visual object.`
            },
            {
                title:'Approach - 04',
                content:`Build everything else around it.`
            }
        ]

        this.createSection3ButtonsClick()

    }

    // Create click events for 'skills' & 'approach' buttons
    createSection3ButtonsClick()
    {
        this.section3Buttons.forEach((btn)=>
        {
            btn.addEventListener('click',()=>
            {
                if(btn.id == 'skills')
                {
                    this.createSkillsButtonFunction()
                    this.createNextSubButton(this.skillsData, this.skillsCounter)
                    this.createCloseSubButton(this.skillsCounter)
                }
                else
                {
                    this.createApproachButtonFunction()
                    this.createNextSubButton(this.approachData, this.approachCounter)
                    this.createCloseSubButton(this.approachCounter)
                }
            })
        })
    }

    // Skills button functionality
    createSkillsButtonFunction()
    {
        this.skillsDetails.children[0].textContent = this.skillsData[0].title
        this.skillsDetails.children[1].textContent = this.skillsData[0].content
        gsap.to(this.experience.world.skillsMesh.position, {duration:0.5, y:-3.6})
        gsap.to(this.experience.world.skillsMesh.position, {duration:0.5, x:0})
        gsap.to('#approach',{duration:0.5,opacity:0})
        gsap.to('.section3-content' , {duration:0.5, opacity:0})
        gsap.to('.section3-skills-details', {duration:0.5, 'clipPath':'polygon(0 0, 100% 0, 100% 100%, 0 100%)',opacity:1})
    }

    // Approach button functionality
    createApproachButtonFunction()
    {
        this.skillsDetails.children[0].textContent = this.approachData[0].title
        this.skillsDetails.children[1].textContent = this.approachData[0].content
        gsap.to(this.experience.world.skillsMesh.position, {duration:0.5, y:-3.6})
        gsap.to(this.experience.world.skillsMesh.position, {duration:0.5, x:0})
        gsap.to('#approach',{duration:0.5,opacity:0})
        gsap.to('.section3-content' , {duration:0.5, opacity:0})
        gsap.to('.section3-skills-details', {duration:0.5, 'clipPath':'polygon(0 0, 100% 0, 100% 100%, 0 100%)',opacity:1})
    }

    // Create next subbutton
    createNextSubButton(data, counter)
    {
        this.skillsNextButton.addEventListener('click',(e)=>
        {
            counter++
            if(counter<data.length)
            {
                this.skillsDetails.children[0].textContent = data[counter].title
                this.skillsDetails.children[1].textContent = data[counter].content
            }
        })
    }

    // Create close subbutton
    createCloseSubButton(counter)
    {
        this.closeSkillsButton.addEventListener('click',()=>
        {
            counter = 0
            gsap.to('#skills',{duration:0.5,opacity:1})
            gsap.to('#approach',{duration:0.5,opacity:1})
            gsap.to(this.experience.world.skillsMesh.position, {duration:0.5, y:-4})
            gsap.to(this.experience.world.skillsMesh.position, {duration:0.5, x:-1})
            gsap.to('.section3-content' , {duration:0.5, opacity:1})
            gsap.to('.section3-skills-details', {duration:0.5, 'clipPath':'polygon(0 0, 0 0, 0 100%, 0% 100%)', opacity:0})
        })
    }
}

export default SkillsButtons