import * as THREE from 'three'
import gsap from 'gsap'

import Model from './Model.js'
import Mouse from '../Utils/Mouse.js'
import Projects from '../Utils/Projects.js'
import Environment from './Environment.js'

class World
{
    constructor(experience)
    {
        // Properties
        this.experience = experience
        this.sizes = this.experience.sizes
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.time = this.experience.time

        this.varyingTime = 0.0005

        this.createLoadingMesh()

        // Create meshes & instances when resources are loaded
        this.resources.on('loaded' , ()=>
        {
            this.showElementsOnLoad()

            this.model = new Model(this.experience)
            this.mouse = new Mouse(this.experience)
            this.projects = new Projects(this.experience)
            this.environment = new Environment(this.experience)

            // Create meshes
            this.createAttributes()
            this.createHeadMesh()
            this.createRaycasterPlane()
            this.createMainMesh()
            this.createSkills()
            this.createMouseMesh()
            this.createBackgroundPointsZero()
            this.createBackgroundPointsOne()
            this.createProjectMesh()
            this.createProjectsUniversalMesh()

            gsap.delayedCall(1,()=>{
                gsap.to(this.mainMesh.material.uniforms.uAmplifier, {duration:7, value:1})
            })
        })
    }

    // Create loading mesh
    createLoadingMesh()
    {
        this.loadingMesh = new THREE.Mesh(
            new THREE.CircleGeometry( 0.2, 15 , 0 , 4 ),
            new THREE.MeshBasicMaterial({
                color:0xffffff,
                wireframe:true
            })
        )
        
        this.scene.add(this.loadingMesh)
    }

    showElementsOnLoad()
    {
        gsap.to('.sidebar', {duration:0.5, opacity:1})
        gsap.to('.section1-scroll', {duration:0.5, opacity:1})
        gsap.to('.loading-plane' , {duration:0.5 , opacity:0 , x:this.sizes.width})
        gsap.to('.nav' , {duration:0.5 , opacity:1})
        gsap.to('.progress' , {duration:0.5 , opacity:1})
        gsap.to('.section1-text-wrapper',{duration:1 , 'clipPath':'polygon(0% 100%, 100% 100%, 100% 0%,0% 0%)'})
        gsap.to(this.experience.camera.instance.rotation, {duration:1 , y:Math.PI * 2 ,ease: "slow(0.7, 0.7, false)"})
        this.loadingMesh.visible = false
    }

    // Create main shape
    createAttributes()
    {
        this.geometry = this.model.model.children[0].geometry
        this.geometry.center()

        this.particlesCount = this.geometry.attributes.position.array.length
        this.randomSizes = new Float32Array(this.particlesCount/3)
        this.randomColors = new Float32Array(this.particlesCount/3)
        this.offset = new Float32Array(this.particlesCount /3)

        for(let i=0; i<this.particlesCount / 3; i++)
        {
            this.randomSizes[i] = Math.random()
            this.randomColors[i] = Math.random()
            this.offset[i] = Math.random() * 10
        }

        this.geometry.setAttribute('aRandomSizes' , new THREE.BufferAttribute(this.randomSizes,1))
        this.geometry.setAttribute('aRandomColors' , new THREE.BufferAttribute(this.randomSizes,1))
        this.geometry.setAttribute('aOffset' , new THREE.BufferAttribute(this.offset,1))
    }

    createHeadMesh()
    {
        this.headGeometry = null

        this.model.headModel.traverse((child)=>
        {
            if(child instanceof THREE.Mesh)
            {
                this.headGeometry = child.geometry
            }
        })
        this.headGeometry.center()

        this.headMesh = new THREE.Points(
            this.headGeometry,
            new THREE.ShaderMaterial({
                vertexShader: this.experience.headVertex,
                fragmentShader:this.experience.headFragment,
                uniforms:{
                    uExpander:{value:0.1},
                    uTime:{value:0},
                    uNegator:{value:0}
                }
            })
        )
        this.headMesh.scale.set(2.8,2.8,2.8)
        this.headMesh.rotation.set(-Math.PI / 2.2, -0.3 , 0.1)
        this.headMesh.position.set(1,-2.2,4)
        this.scene.add(this.headMesh)
    }

    createMainMesh()
    {
        this.mainMesh = new THREE.Points(
            this.geometry,
            new THREE.ShaderMaterial({
                side: THREE.DoubleSide,
                depthTest: false,
                depthWrite: false,
                blending: THREE.AdditiveBlending,
                transparent:true,
                vertexShader: this.experience.vertex,
                fragmentShader: this.experience.fragment,
                uniforms:{
                    uTime: {value:0},
                    uBrightness:{value:0.01},
                    uAmplifier:{value:0.1},
                    uExpander:{value:1},
                    uColor1: {value: new THREE.Color(0x00aaff)},
                    uColor2: {value: new THREE.Color(0xf62c55)},
                    uColor3: {value: new THREE.Color(0xf02cf6)}
                }
            })
        )
        this.mainMesh.scale.set(1,0.5,0.5)
        this.mainMesh.position.set(1.5,-1,0)
        this.scene.add(this.mainMesh)
    }

    // Create skills cylinder shape
    createSkills()
    {
        this.skillsMesh = new THREE.Mesh(
            new THREE.CylinderBufferGeometry(0.5,0.5,0.5,64,64),
            // new THREE.MeshBasicMaterial({
            //     map:this.resources.items.skillsTexture,
            //     color:0x0000ff,
            //     wireframe:true
            // })
            new THREE.ShaderMaterial({
                transparent:true,
                wireframe:true,
                vertexShader:this.experience.textureVertex,
                fragmentShader:this.experience.textureFragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.resources.items.skillsTexture}
                }
            })
        )
        this.skillsMesh.rotation.x = 0.5
        this.skillsMesh.position.set(-1,-4,6)
        
        this.scene.add(this.skillsMesh)
    }

    // Create background points 1 & 0
    createBackgroundPointsZero()
    {
        this.backPointsOne = new THREE.Points(
            new THREE.IcosahedronBufferGeometry(4,4,4),
            new THREE.ShaderMaterial({
                transparent:true,
                depthTest:false,
                depthWrite:false,
                blending: THREE.AdditiveBlending,
                vertexShader:this.experience.backPointsV,
                fragmentShader:this.experience.backPointsF,
                uniforms:{
                    uTime:{value:0},
                    uPointsTexture:{ value:this.resources.items.pointsZeroTexture},
                    uAmplifier:{value:2}
                }
            })
        )
        this.scene.add(this.backPointsOne)
    }

    createBackgroundPointsOne()
    {
        this.backPoints = new THREE.Points(
            new THREE.IcosahedronBufferGeometry(3,2,2),
            new THREE.ShaderMaterial({
                transparent:true,
                depthTest:false,
                depthWrite:false,
                blending: THREE.AdditiveBlending,
                vertexShader:this.experience.backPointsV,
                fragmentShader:this.experience.backPointsF,
                uniforms:{
                    uTime:{value:0},
                    uPointsTexture:{ value:this.resources.items.pointsOneTexture},
                    uAmplifier:{value:1}
                }
            })
        )
        this.backPoints.position.z = -5
        this.scene.add(this.backPoints)
    }

    // Create raycaster objects
    createMouseMesh()
    {
        this.mouseMesh = new THREE.Mesh(
            new THREE.SphereBufferGeometry(0.1,16,16),
            new THREE.MeshBasicMaterial()
        )
        this.mouseMesh.position.y = 0
        this.scene.add(this.mouseMesh)
    }

    createRaycasterPlane()
    {
        this.raycasterPlane = new THREE.Mesh(
            new THREE.PlaneBufferGeometry(
                this.experience.sizes.width ,
                this.experience.sizes.height,
                1
            ),
            new THREE.MeshStandardMaterial({color:0x04002a})
        )
        this.raycasterPlane.position.z = -3
        this.raycasterPlane.receiveShadow = true
        this.scene.add(this.raycasterPlane)
    }

    // Projects meshes
    createProjectMesh()
    {
        this.projectMesh = new THREE.Mesh(
            new THREE.PlaneBufferGeometry(2,1,1),
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:this.experience.textureVertex,
                fragmentShader:this.experience.textureFragment,
                uniforms:{
                    uAmplifier:{value:1},
                    uTime:{value:0},
                    uTexture:{value:this.resources.items.project1Texture},
                }
            })
        )

        this.projectMesh.rotation.y = Math.PI * 2
        this.projectMesh.position.set(1,-6,0)
        this.scene.add(this.projectMesh)
    }

    createProjectsUniversalMesh()
    {
        this.projectUniversalShape = new THREE.Mesh(
            new THREE.PlaneBufferGeometry(5,2.5,1),
            new THREE.ShaderMaterial({
                side:THREE.DoubleSide,
                vertexShader:this.experience.textureVertex,
                fragmentShader:this.experience.textureFragment,
                uniforms:{
                    uTime:{value:0},
                    uTexture:{value:this.resources.items.project1Texture},
                    uNegator:{value:1}
                }
            })
        )
        this.projectUniversalShape.visible = false
        this.projectUniversalShape.rotation.z = Math.PI
        this.projectUniversalShape.rotation.y = Math.PI
        this.projectUniversalShape.position.set(0,0.5,7)
        this.scene.add(this.projectUniversalShape)
    }

    update()
    {
        if(this.mainMesh)
        {
            // Update uniforms
            this.mainMesh.material.uniforms.uTime.value = this.time.elapsed * 0.0002
            this.backPoints.material.uniforms.uTime.value = this.time.elapsed * 0.00001
            this.backPointsOne.material.uniforms.uTime.value = this.time.elapsed * 0.00001
            this.projectMesh.material.uniforms.uTime.value = this.time.elapsed * 0.00001
            this.headMesh.material.uniforms.uTime.value = this.time.elapsed * 0.00001
            this.projectUniversalShape.material.uniforms.uTime.value = this.time.elapsed * 0.00001

            // Animate meshes
            this.mainMesh.rotation.y = this.time.elapsed * 0.0001
            this.skillsMesh.rotation.y = this.time.elapsed * this.varyingTime
            this.loadingMesh.rotation.z = this.time.elapsed * 0.001
        }

        // Update mouse
        if(this.mouse)
        {
            this.mouse.update()
        }
    }
}

export default World