import gsap from 'gsap'

class Projects
{
    constructor(experience)
    {
        this.experience = experience

        // DOM Elements
        this.projectsTitle = document.querySelector('.projects-showcase-title')
        this.projectsDetails = document.querySelector('.projects-showcase-desc').children[0]
        this.projectsDetailsSpan1 = document.querySelector('.projects-showcase-desc').children[1].children[0]
        this.projectsDetailsSpan2 = document.querySelector('.projects-showcase-desc').children[1].children[1]
        this.projectsLink = document.querySelector('.projects-showcase-link')
        this.openProjectsBtn = document.querySelector('.open-projects-btn')
        this.closeProjectsBtn = document.querySelector('.close-projects-btn')
        this.nextProjectBtn = document.querySelector('.projects-showcase-next-btn')
        this.previousProjectBtn = document.querySelector('.projects-showcase-prev-btn')

        // Data 
        this.projectsData = [
            {
                name:'Groundrock',
                info:'Website for a greek digital marketing business. I have developed the page from scratch using three.js & GLSL shaders for the visuals.',
                infoSpan1:'Groundrock',
                infoSpan2:'Technologies: Three.js, GLSL & Gsap',
                texture:this.experience.resources.items.project1Texture,
                link:'https://sizzysiks.github.io/groundrock_v4/'
            },
            {
                name:'Anila',
                info:'Personal portfolio for poet & activist Anila Kila. A minimalistic black & white approach complimented by visuals done with GLSL Shaders',
                infoSpan1:'Anila Kila',
                infoSpan2:'Technologies: Three.js, GLSL & Gsap',
                texture:this.experience.resources.items.project2Texture,
                link:'https://sizzysiks.github.io/anila-kila-v1/'
            },
            {
                name:'Animated 3D Model',
                info:'Experimenting with 3d models & animations. The model is built with Blender and is imported using Draco compressor to optimize the file size. The animations are controlled through a debug panel.',
                infoSpan1:'Experiment',
                infoSpan2:'Technologies: Three.js, Blender & Gltf',
                texture:this.experience.resources.items.project3Texture,
                link:'https://sizzysiks.github.io/3d_animated_model/#debug'
            },
            {
                name:'Physics',
                info:'Experimenting with physics using cannon.js. The scene consists of an object generator creating 3d objects which on collision behave as real life objects would.',
                infoSpan1:'Experiment',
                infoSpan2:'Technologies: Three.js, Cannon.js',
                texture:this.experience.resources.items.project4Texture,
                link:'https://sizzysiks.github.io/physics/'
            },
            {
                name:'Last portfolio',
                info:'The first portfolio of myself before creating the existing one. Built with three.js and custom shaders.',
                infoSpan1:'Personal portfolio',
                infoSpan2:'Technologies: Three.js, GLSL & Gsap',
                texture:this.experience.resources.items.project5Texture,
                link:'https://sizzysiks.github.io/antino_old_portfolio/'
            },
            {
                name:'Pics 2 Particles',
                info:'Transforming images into particles with complete control of each part. Mouse move or click creates a radius inside which the particles scatter in different speeds and space.',
                infoSpan1:'Experiment',
                infoSpan2:'Technologies: Three.js, GLSL & Gsap',
                texture:this.experience.resources.items.project6Texture,
                link:'https://sizzysiks.github.io/pics_to_particles/'
            }
        ]
        this.counter = 0
        this.rotation = 1
        this.projectsTitle.textContent = this.projectsData[this.counter].name
        this.projectsDetails.textContent = this.projectsData[this.counter].info

        // Calling methods
        this.createOpenProjectsClick()
        this.createCloseProjectsClick()
        this.createNextProjectClick()
        this.createPreviousProjectClick()
    }

    // Click event for opening projects
    createOpenProjectsClick()
    {
        this.openProjectsBtn.addEventListener('click',()=>
        {
            gsap.to(this.closeProjectsBtn,{duration:0.5,opacity:1})
            document.body.style.overflowY = 'hidden'
            gsap.to('.sidebar', {duration:0.5, opacity:0})
            gsap.to('.projects-showcase',{duration:0.2,'scaleX':'1',opacity:1})
            gsap.to('.nav',{duration:0.5,opacity:0})
            gsap.to('.section1',{duration:0.5,opacity:0})
            gsap.to('.progress',{duration:0.5,opacity:0})
            this.experience.world.projectUniversalShape.visible = true
            this.experience.world.backPoints.visible = false
            this.experience.world.backPointsOne.visible = false
            gsap.to(this.experience.camera.instance.rotation,{duration:1, x:Math.PI})
            gsap.to(this.experience.renderer.unrealBloom,{duration:0.5,enabled: false})
        })
    }

    // Click event for closing projects
    createCloseProjectsClick()
    {
        this.closeProjectsBtn.addEventListener('click',()=>
        {
            gsap.to(this.closeProjectsBtn,{duration:0.5,opacity:0})
            document.body.style.overflowY = 'auto'
            gsap.to('.sidebar', {duration:0.5, opacity:1})
            gsap.to('.projects-showcase',{duration:0.2,'scaleX':'0',opacity:0})
            gsap.to('.nav',{duration:0.5,opacity:1})
            gsap.to('.section1',{duration:0.5,opacity:1})
            gsap.to('.progress',{duration:0.5,opacity:1})
            this.experience.world.projectUniversalShape.visible = false
            this.experience.world.backPoints.visible = true
            this.experience.world.backPointsOne.visible = true
            gsap.to(this.experience.camera.instance.rotation,{duration:1, x:0})
            gsap.to(this.experience.renderer.unrealBloom,{duration:0.5,enabled:true})
        })
    }

    // Next project button functionality
    createNextProjectClick()
    {
        this.nextProjectBtn.addEventListener('click',()=>
        {
            gsap.to(this.experience.world.projectUniversalShape.rotation,{duration:1 , y: Math.PI * this.rotation})
            // gsap.to(this.experience.world.projectUniversalShape.rotation,{duration:1 , z: Math.PI * this.rotation})
            this.counter++
            if(this.counter == this.projectsData.length)
            {
                this.counter = 0
                this.projectsTitle.textContent = this.projectsData[this.counter].name
                this.projectsDetails.textContent = this.projectsData[this.counter].info
                this.projectsDetailsSpan1.textContent = this.projectsData[this.counter].infoSpan1
                this.projectsDetailsSpan2.textContent = this.projectsData[this.counter].infoSpan2
                this.projectsLink.href = this.projectsData[this.counter].link
                this.experience.world.projectUniversalShape.material.uniforms.uTexture.value = this.projectsData[this.counter].texture
            }
            else 
            {
                this.projectsTitle.textContent = this.projectsData[this.counter].name
                this.projectsDetails.textContent = this.projectsData[this.counter].info
                this.projectsDetailsSpan1.textContent = this.projectsData[this.counter].infoSpan1
                this.projectsDetailsSpan2.textContent = this.projectsData[this.counter].infoSpan2
                this.projectsLink.href = this.projectsData[this.counter].link
                this.experience.world.projectUniversalShape.material.uniforms.uTexture.value = this.projectsData[this.counter].texture
            }
            this.rotation += 2
        })
    }

    // Previous project button functionality
    createPreviousProjectClick()
    {
        this.previousProjectBtn.addEventListener('click',()=>
        {
            gsap.to(this.experience.world.projectUniversalShape.rotation,{duration:1 , y: Math.PI * this.rotation})
            // gsap.to(this.experience.world.projectUniversalShape.rotation,{duration:1 , z: -Math.PI * this.rotation})
            this.counter--
            if(this.counter < 0)
            {
                this.counter = this.projectsData.length-1
                this.projectsTitle.textContent = this.projectsData[this.counter].name
                this.projectsDetails.textContent = this.projectsData[this.counter].info
                this.projectsDetailsSpan1.textContent = this.projectsData[this.counter].infoSpan1
                this.projectsDetailsSpan2.textContent = this.projectsData[this.counter].infoSpan2
                this.projectsLink.href = this.projectsData[this.counter].link
                this.experience.world.projectUniversalShape.material.uniforms.uTexture.value = this.projectsData[this.counter].texture
            }
            else 
            {
                this.projectsTitle.textContent = this.projectsData[this.counter].name
                this.projectsDetails.textContent = this.projectsData[this.counter].info
                this.projectsDetailsSpan1.textContent = this.projectsData[this.counter].infoSpan1
                this.projectsDetailsSpan2.textContent = this.projectsData[this.counter].infoSpan2
                this.projectsLink.href = this.projectsData[this.counter].link
                this.experience.world.projectUniversalShape.material.uniforms.uTexture.value = this.projectsData[this.counter].texture
            }
            this.rotation += 2
        })
    }
}

export default Projects
    